.channel-page{

    .p1th{
        .item{
            margin-bottom: 30px;
            width: 1000px;
            position: relative;
            height:320px;
            overflow: hidden;
            .txt-area{
                position: absolute;
                width: 100%;
                height:90px;
                bottom: 0;
                left: 0;
                background: url(../../images/line-10x100.png ) repeat-x;
                color:#fff;
                .title{
                    padding-left: 20px;
                    padding-top: 15px;
                }
                .des{
                    padding-left: 20px;
                    padding-top: 3px;
                }
            }

            .fade-h5{
                width:100%; 
                height:90px; 
                position:absolute;  
                bottom:-90px; 
                left:0;
                filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#7F000000', endColorstr='#7F000000');background:rgba(0,0,0,0.5);
                box-sizing: border-box;
                transition: bottom 0.1s ease-out;
            }


            &:hover{
                .fade-h5{
                    bottom:0
                }
            }
        }

        .page{
            padding-top: 50px;
        }
        
    }
}